@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Bungee+Spice&family=Ubuntu:wght@300;400;500;700&display=swap');

body, html {
  width: 100%;
  min-height: 100%;
  position: relative;
}
body, html, input, button {
  font-family: 'Ubuntu', sans-serif;
}


*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
